import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/Home'
import NeedDetail from "../components/NeedDetail";
import NeedHome from "../components/NeedHome";
import NeedRelease from "../components/NeedRelease";
const Login = resolve => require(['../components/Login'], resolve);
const Register = resolve => require(['../components/Register'], resolve);
const forgetPW = resolve => require(['../components/forgetPW'], resolve);
const Introduce = resolve => require(['../components/Introduce'], resolve);
const Shome = resolve => require(['../components/InfoManage/Shome'], resolve);
const ScienceEntrance = resolve => require(['../components/InfoManage/ScienceEntrance'], resolve);
const Thome = resolve => require(['../components/InfoManage/Thome'], resolve);
const Achievement = resolve => require(['../components/InfoManage/Achievement'], resolve);
const Ihome = resolve => require(['../components/Industrial/IndusHome'], resolve);
const IndusForm = resolve => require(['../components/Industrial/IndusForm'], resolve);
const IndusIndex = resolve => require(['../components/Industrial/IndusIndex'], resolve);
const IndusChart = resolve => require(['../components/Industrial/IndusChart'], resolve);
const form1 = resolve => require(['../components/Industrial/IndusForm/form1'], resolve);
const form2 = resolve => require(['../components/Industrial/IndusForm/form2'], resolve);
const form3 = resolve => require(['../components/Industrial/IndusForm/form3'], resolve);
const form4 = resolve => require(['../components/Industrial/IndusForm/form4'], resolve);
const form5 = resolve => require(['../components/Industrial/IndusForm/form5'], resolve);
const form7 = resolve => require(['../components/Industrial/IndusForm/form7'], resolve);
const form8 = resolve => require(['../components/Industrial/IndusForm/form8'], resolve);
const form9 = resolve => require(['../components/Industrial/IndusForm/form9'], resolve);
const EnterpriseCenter = resolve => require(['../components/EnterpriseCenter/EnterpriseCenter'], resolve);
const NItemDetails= resolve => require(['../components/NItemDetails'], resolve);
const NeedIndex= resolve => require(['../components/NeedIndex'], resolve);
const IndusFinanc= resolve => require(['../components/Industrial/IndusFinanc'], resolve);
const Torganize= resolve => require(['../components/InfoManage/Torganize'], resolve);
const Tachieve= resolve => require(['../components/InfoManage/Tachieve'], resolve);
const TCompanyDetail = resolve => require(['../components/InfoManage/TCompanyDetail'], resolve);
const TachieveDetails = resolve => require(['../components/InfoManage/TachieveDetails'], resolve);
const InnovativeActivities = resolve => require(['../components/InnovativeActivities'], resolve);
const IndexHome = resolve => require(['../components/Home/Home'], resolve);
Vue.use(Router)

export default new Router({
  routes: [
    {
      path:'/enterpriseCenter',
      name:'EnterpriseCenter',
      component:EnterpriseCenter
    },
    {
      path: '/Home',
      name: 'Home',
      component: Home,
      children:[
        {
          path: '/Home/NeedDetail',
          name:'NeedDetail',
          component: NeedDetail
        },
        {
          path: '/Home/NeedHome',
          name: 'NeedHome',
          component: NeedHome
        },
        {
          path: '/Home/NeedRelease',
          name: 'NeedRelease',
          component: NeedRelease
        },
        {
          path: '/Home/NItemDetails',
          name: 'NItemDetails',
          component: NItemDetails
        },
        {
          path: '/Home/NeedIndex',
          name: 'NeedIndex',
          component: NeedIndex
        }
      ]
    },
    {
      path:'/iHome',
      name:'iHome',
      component:Ihome,
      children:[
        {
          path:'/iHome/IndusForm',
          name:'IndusForm',
          component:IndusForm,
        },
        {
          path:'/iHome/IndusIndex',
          name:'IndusIndex',
          component:IndusIndex
        },
        {
          path:'/iHome',
          redirect:'/iHome/IndusIndex'
        },
        {
          path:'/iHome/IndusChart',
          name:'IndusChart',
          component:IndusChart
        },
        {
          path:'/iHome/IndusFinanc',
          name:'IndusChart',
          component:IndusFinanc
        },
        {
          path:'/iHome/form1',
          name:'form1',
          component:form1
        },
        {
          path:'/iHome/form2',
          name:'form2',
          component:form2
        },
        {
          path:'/iHome/form3',
          name:'form3',
          component:form3
        },
        {
          path:'/iHome/form4',
          name:'form4',
          component:form4
        },
        {
          path:'/iHome/form5',
          name:'form5',
          component:form5
        },
        {
          path:'/iHome/form7',
          name:'form7',
          component:form7
        },
        {
          path:'/iHome/form8',
          name:'form8',
          component:form8
        },
        {
          path:'/iHome/form9',
          name:'form9',
          component:form9
        }
      ]
    },
    {
      path:'/sHome',
      name:'sHome',
      component:Shome,
      children:[
        {
          path:'/sHome/ScienceEntrance',
          name:'ScienceEntrance',
          component:ScienceEntrance
        },
        {
          path:'/sHome/TachieveDetails',
          name:'TachieveDetails',
          component:TachieveDetails
        },
        {
          path:'/sHome/Torganize',
          name:'Torganize',
          component:Torganize
        },
        {
          path:'/sHome/Tachieve',
          name:'Tachieve',
          component:Tachieve
        },
        {
          path:'/sHome/TCompanyDetail',
          name:'TCompanyDetail',
          component:TCompanyDetail
        },
        {
          path:'/sHome/details',
          name:'Thome',
          component:Thome
        },
        {
          path:'/sHome/achievement',
          name:'Achievement',
          component:Achievement
        },
        {
          path:'/sHome',
          redirect:'/sHome/details'
        }
      ]
    },
    {
      path:'/login',
      component:Login
    },
    {
      path:'/register',
      component:Register
    },
    {
      path:'/Introduce',
      component:Introduce,
      children:[
        {
          path:'/Introduce/home',
          name:'Home',
          component:IndexHome
        },
        {
          path:'/Introduce/activis',
          name:'InnovativeActivities',
          component:InnovativeActivities
        }
      ]
    },
    {
      path:'/forgetPW',
      component: forgetPW,
    },
    {
      path:'/Introduce',
      redirect:'/Introduce/home'
    },
    {
      path:'/',
      redirect:'/Introduce/home'
    },
  ]
})



