import request from "./http"
import QS from 'qs'
export function getData(params) {
    return request({
        method:'get',
        url:'',
        params:params
    })
}
export function putData(data) {
    return request({
        method:'post',
        url:'',
        data
    })
}
/*******
 * 工业项目申报
 * */
//工业项目申报查询接口
export function getindustrialdeclarationData(params) {
    return request({
        method:'get',
        url:'/industrialdeclaration/showByPageForCenter',
        params
    })
}
//工业申报项目详情接口
export function getindustrialdeclarationDetail(params) {
    return request({
        method:'get',
        url:'/industrialdeclaration/getById',
        params
    })
}
//工业项目申报删除详情接口
export function delindustrialdeclarationDetail(data) {
    return request({
        method:'POST',
        url:'/industrialdeclaration/deleteById',
        data:QS.stringify(data)
    })
}

/*******
 * 企业需求
 * */
export function getDemandsByID(params) {
    return request({
        method:'get',
        url:'/demands/getDemandsByID',
        params
    })
}
//修改需求/demands/updateDemands
export function editDemandsDetailByID(data) {
    return request({
        method:'POST',
        url:'/demands/updateDemands',
        data:QS.stringify(data)
    })
}



