                        demandType = demandType + item + ','
                    }

                })
                if (a){
                    demandType = '';
                }

                demandType = demandType.substring(0,demandType.length-1);
                console.log(demandType)
                var cooperationType = '';  //合作方式
                var b = false;
                this.form.cooperationType.forEach((item,key)=>{
                    if (item=='不限'){
                        cooperationType = '';
                        b = true;
                        return false
                    }else {
                        cooperationType = cooperationType + item + ','
                    }
                })
                if (b){
                    cooperationType = '';
                }
                cooperationType = cooperationType.substring(0,cooperationType.length-1);
                var cooperationIntention = '';
                var c = '';
                this.form.cooperationIntention.forEach((item,key)=>{
                    if (item=='不限'){
                        cooperationIntention = '';
                        c = true;
                        return false
                    }else {
                        cooperationIntention = cooperationIntention + item + ','
                    }
                })
                if (c){
                    cooperationIntention = '';
                }
                cooperationIntention = cooperationIntention.substring(0,cooperationIntention.length-1);
                var keyWord = this.form.keyWord;
                var pageNum = val||1;
                that.$axios.get('/demands/showByPage', {
                    params: {
                        demandIndustry: demandIndustry,
                        demandIndustryDetail: demandIndustryDetail,
                        preInvestmentAmountBegin: preInvestmentAmountBegin,
                        preInvestmentAmountEnd: preInvestmentAmountEnd,
                        demandType: demandType,
                        cooperationType: cooperationType,
                        cooperationIntention: cooperationIntention,
                        pageNum: pageNum,
                        keyWord: keyWord,
                        pageSize: 9
                    }
                })
                    .then(res=>{
                        console.log(res)
                        if (res.data.code == 0){
                            if (res.data.data.total == 0){
                                this.$message({
                                    message: '无筛选数据',
                                    type: 'warning'
                                });
                            }else {

                                if (that.falg){
                                    this.$message({
                                        message: '查询成功',
                                        type: 'success'
                                    });
                                }
                                that.falg = false;
                                that.info = res.data.data.list;
                                console.log(res.data.data.list);
                                that.total = res.data.data.total;
                            }
                        }
                    })

            },
            selectStyle(i){
                this.isActive[i] = true;
            },
            outStyle(i) {
                this.isActive[i] = false;
            },
            //获取企业需求信息
            getInfo(i){
                var pageNum = i || 1;
                var that = this;
                that.$axios.get('/demands/showByPage',{
                    params:{
                        pageNum:pageNum,
                        pageSize:9
                    }
                }).then((res)=>{
                    console.log(res)
                    if (res.data.code == 0){
                        that.info = res.data.data.list;
                        console.log(res.data.data.list);
                        that.total = res.data.data.total;
                    }
                })
            },

            //页面改变
            handleSizeChange(val) {
                console.log(`每页 ${val} 条`);
            },
            handleCurrentChange(val) {
                console.log(`当前页: ${val}`);
                if (this.falg == true){
                    this.getInfo(val);
                }else {
                    this.onSubmit(val)
                }
            },
            //时间转换
                //转换时间格式
                formateDate(datetime) {
                    function addDateZero(num) {
                        return (num < 10 ? "0" + num : num);
                    }
                    let d = new Date(datetime);
                    let formatdatetime = d.getFullYear() + '-' + addDateZero(d.getMonth() + 1) + '-' + addDateZero(d.getDate()) + ' ' + addDateZero(d.getHours()) + ':' + addDateZero(d.getMinutes()) + ':' + addDateZero(d.getSeconds());
                    return formatdatetime;
                },
            handleChange1(){

            }


        },
    }
</script>

