<template>
  <div>
    <div class="head">
      <div class="head-wrap">
        <div class="welcome">
          <span>{{day}}，</span>
          <span>欢迎来到政企智慧服务平台</span>
        </div>
        <div class="login" >
          <span @click="$router.push('/login')" style="cursor: pointer" v-if="!username">登陆</span>
        </div>
        <div class="login1">
          <span  style="cursor: pointer" v-if="username">欢迎您！{{username}}</span>
          <span v-if="username" @click="$router.push('/enterpriseCenter')" style="display: inline-block;margin-left: 10px;font-size: 14px;cursor: pointer;color: #1e58a3">企业中心</span>
          <span v-if="username" style="display: inline-block;margin-left: 10px;font-size: 14px;cursor: pointer;color: #1e58a3" @click="goBack">退出</span>
        </div>
        <div class="register">
          <span @click="$router.push('/register')" style="cursor: pointer" v-if="!username">注册</span>
        </div>
        <div class="wx" @mouseenter="isCode=true"
             @mouseleave="isCode=false"
        >
          <span  style="cursor: pointer">微信公众号</span>
          <img src="../assets/code.jpg" alt="" style="width: 160px;position: absolute;" class="code" v-show="isCode">

        </div>
      </div>

    </div>
    <div class="search">
      <div class="search-text">
        <span style="cursor: pointer" @click="$router.push('/Introduce/home')">政企智慧服务平台</span>
        <span v-if="$route.path.indexOf('Home/NeedIndex')>=0">>企业科技需求征集系统</span>
      </div>
      <div class="search-input">
        <input type="text" placeholder="请输入关键词"/>
        <span>搜索</span>
      </div>
    </div>
    <div class="banner" v-if="$route.path.indexOf('Home/NeedIndex') >= 0">
      <img src="../assets/banner8.png" alt="" style="vertical-align: bottom">
    </div>
    <div class="need-head">
      <div class="need-head-warp">
        <ul>
          <li style="cursor: pointer" :class="$route.path.indexOf('Home/NeedIndex') >= 0 ?'active':''" @click="$router.push('/Home/NeedIndex')">首页</li>
          <li style="cursor: pointer" :class="$route.path.indexOf('Home/NeedHome') >= 0 ?'active':''" @click="$router.push('/Home/NeedHome')">需求大厅</li>
        </ul>
      </div>
    </div>
    <router-view></router-view>
    <foot></foot>
  </div>
</template>

<script>
    import foot from './foot'
    export default {
        name: "BaseHead",
        data(){
            return {
                username:'',
                isCode:false,
                day:''
            }
        },
        created(){
            var username = sessionStorage.getItem('username');
            this.username = username;
            console.log(this.username);
            this.getTime();
        },
        methods:{
            //获取最新时间
            getTime(){
                var myDate = new Date();
                var year = myDate.getFullYear();
                var month = myDate.getMonth() + 1;
                var day  =  myDate.getDate();
                console.log(year+'-' + month + '-' +day)
                this.day = year+'-' + month + '-' +day;

            },
            //退出
            goBack(){
               var that = this;
                this.$confirm('此操作退出账户, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    this.$message({
                        type: 'success',
                        message: '退出登陆!'
                    });
                    that.$axios.get('user/logout')
                        .then(res=>{
                            console.log(res)
                        })
                    sessionStorage.removeItem('username');
                    that.$router.push('/')
                    location.reload()
                    that.$router.go(0)
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消'
                    });
                });
            },
        },
        components:{
            foot
        }
    }
</script>

<style scoped lang="less">
  .head {
    width: 100%;
    height: 40px;
    background-color: #f1f1f1;
    border-bottom: 1px solid #dcdcdc;
    line-height: 40px;
    font-size: 12px;
  }
  ::placeholder { /* Firefox, Chrome, Opera */
    color: #bababa;
    font-size: 16px;
  }
  :-ms-input-placeholder { /* Internet Explorer 10-11 */
    color: #bababa;
    font-size: 16px;
  }
  ::-ms-input-placeholder { /* Microsoft Edge */
    color: #bababa;
    font-size: 16px;
  }
  .head-wrap {
    width: 1200px;
    margin: 0 auto;
    color: #7c7c7c;
    position: relative;
  }
  .login {
    position: absolute;
    right: 114px;
    top: 0;
  }
  .wx {
    position: absolute;
    right: 0;
    top: 0;
  }
  .register {
    position: absolute;
    right: 82px;
    top: 0;
  }
  .search-text {
    span:nth-child(1){
      font-size: 32px;
      color: #3f3f3f;
    }
    span:nth-child(2){
      color: #3f3f3f;
      font-size: 22px;
    }
  }
  .search {
    width: 1200px;
    margin: 0 auto;
    height: 116px;
    line-height: 116px;
    box-sizing: border-box;
    background-size: cover;
    position: relative;
  }
  .search-input {
    width: 432px;
    height: 42px;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    box-sizing: border-box;
    right: 0;
  }
  .search-input input {
    width: 346px;
    height: 43px;
    outline: none;
    border-radius: 4px 0 0 4px;
    border: solid 1px #cacaca;
    position: absolute;
    left: 0;
    top: 0;
    box-sizing: border-box;
    padding-left: 16px;
  }
  .search-input span {
    display: inline-block;
    width: 86px;
    height: 42px;
    text-align: center;
    line-height: 42px;
    border-radius: 0 4px 4px 0;
    border: solid 1px #cacaca;
    position: absolute;
    background-color: #1e58a3;
    right: 0;
    top: -1px;
    color: #ffffff;
    font-size: 18px;
  }
  .search-com input:focus {
    border: none;
    outline: none;
  }
  .need-head {
    width: 100%;
    height: 62px;
    background:rgba(30,88,163,1);
  }
  .active {
    background-color: #008fff;
  }
  .need-head-warp {
    width: 1209px;
    height: 62px;
    margin: 0 auto;
    ul {
      overflow: hidden;
      li {
        list-style: none;
        padding-left: 30px;
        padding-right: 25px;
        line-height: 62px;
        float: left;
        color: #ffffff;
        font-size: 20px;
      }
    }
  }
  .banner {
    width: 100%;
    height: 100%;
    min-width: 1200px;
    img {
      width: 100%;
      height: 100%;
      min-width: 1200px;
    }
  }
  .login1 {
    position: absolute;
    right: 83px;
    top: 0;
  }
  .code {
    left: -24px;
    top: 41px;
    z-index: 10000;

  }
</style>



