                    for(var k in o) {
                        if(new RegExp("("+ k +")").test(fmt)){
                            fmt = fmt.replace(RegExp.$1, (RegExp.$1.length==1) ? (o[k]) : (("00"+ o[k]).substr((""+ o[k]).length)));
                        }
                    }
                    return fmt;
                }

                if (demandType==''){
                    this.$message({
                        message: '请选择需求类别!',
                        type: 'warning'
                    });
                    return false
                }
                if (cooperationType==''){
                    this.$message({
                        message: '请选择合作方式!',
                        type: 'warning'
                    });
                    return false
                }
                if (cooperationIntention==''){
                    this.$message({
                        message: '请选择合作倾向!',
                        type: 'warning'
                    });
                    return false
                }
                if (!demandIndustry){
                    this.$message({
                        message: '请选择所属行业!',
                        type: 'warning'
                    });
                    return false
                }
                if (demandIndustryDetail==''){
                    this.$message({
                        message: '请选择具体行业!',
                        type: 'warning'
                    });
                    return false
                }
                if (demandOutline==''){
                    this.$message({
                        message: '请填写需求标题!',
                        type: 'warning'
                    });
                    return false
                }

                if (demandContent==''){
                    this.$message({
                        message: '请填写需求详情!',
                        type: 'warning'
                    });
                    return false
                }
                if (expectedResult==''){
                    this.$message({
                        message: '请填写期望实现结果!',
                        type: 'warning'
                    });
                    return false
                }
                if (preInvestmentAmount==''){
                    this.$message({
                        message: '请填写预投金额!',
                        type: 'warning'
                    });
                    return false
                }
                if (contact==''){
                    this.$message({
                        message: '请填写联系人!',
                        type: 'warning'
                    });
                    return false
                }
                if (phoneNum==''){
                    this.$message({
                        message: '请填写手机号!',
                        type: 'warning'
                    });
                    return false
                }
                if (endDate==''){

                    this.$message({
                        message: '请选择需求截止时间!',
                        type: 'warning'
                    });
                    return false
                }else {
                    // var endDate = this.form.endDate.format("yyyy-MM-dd hh:mm:ss");
                    var endDate = this.form.endDate;
                }
                //如果是编辑
                if(this.formDetailData&&this.formDetailData.isEdit){
                    [this.form.demandIndustry,this.form.demandIndustryDetail]=this.value;
                    this.form.userName="admin";
                    // this.form.id
                    editDemandsDetailByID(this.form).then(res=>{
                        console.log(res);
                        if(res.data.code==0){
                            that.$message({
                                showClose: true,
                                message: '修改成功!',
                                type: 'success'
                            });
                            this.$emit("childCloseDia");
                        }else{
                            that.$message({
                                showClose: true,
                                message: '修改失败',
                                type: 'error'
                            });
                        }

                    })
                }else{
                    // 新增
                this.$axios.post('/demands/addDemands', this.$qs.stringify({
                    demandType:demandType,
                    cooperationType:cooperationType,
                    cooperationIntention:cooperationIntention,
                    demandIndustry:demandIndustry,
                    demandIndustryDetail:demandIndustryDetail,
                    demandOutline:demandOutline,
                    demandContent:demandContent,
                    expectedResult:expectedResult,
                    preInvestmentAmount:preInvestmentAmount,
                    contact:contact,
                    phoneNum:phoneNum,
                    endDate:endDate,
                    userName:'admin'

                })).then(function(res) {
                    if (res.data.code==0){
                        that.$message({
                            showClose: true,
                            message: '恭喜，提交成功!',
                            type: 'success'
                        });
                      that.$router.push('/Home/NeedHome')
                    }else {
                        that.$message({
                            showClose: true,
                            message: res.data.msg,
                            type: 'error'
                        });
                    }
                    console.log(res)
                })
                }
            },
            handleChange (value) {
                var arr = [];
                value.forEach((item,index)=>{
                    arr.push(CodeToText[item])
                })
                this.form.city = arr;
                console.log(this.form.city)
            },
            handleChange1(){

            },
            closeDialog(){
                this.$emit("childCloseDia");
            }
        },
        computed:{
            baseSrc:function () {
                return  this.$store.state.src
            }
        },
        created () {
            var that = this;
            var status = sessionStorage.getItem('status');
            if (status==1){

            }else {
                this.$message({
                    message: '未进行企业认证，请先认证',
                    type: 'warning'
                });
                that.$router.push('/enterpriseCenter')
            }
            console.log(this.formDetailData);
            for(let key in this.form){
                this.form[key]=this.formDetailData[key];
            }
            this.value=[this.formDetailData.demandIndustry,this.formDetailData.demandIndustryDetail];
        },
    }
</script>

<style  lang="less">
   .el-form-item__content {
     margin-left: 170px!important;
   }
   .el-form-item__label {
     width: 150px!important;
   }
   .needForm {
    //  width: 1200px;
     margin: 0 auto;
     background-color: #fff;
     box-sizing: border-box;
     padding-left: 96px;
     padding-right: 153px;
     padding-top: 44px;
     .title {
          font-size: 20px;
          color: #333333;
          margin-bottom: 61px;
          font-weight: 600;
          text-align: center;
       span {
         display: inline-block;
         position: relative;

       }
       span:before{
        content: '';
         display: block;
         width: 386px;
         height: 1px;
         position: absolute;
         background: #d2d2d2;
         right: 178px;
         top: 50%;
         transform: translateY(-50%);
       }
       span:after{
         content: '';
         display: block;
         width: 386px;
         height: 1px;
         position: absolute;
         background: #d2d2d2;
         left: 178px;
         top: 50%;
         transform: translateY(-50%);
       }
     }

   }
  .needWarp {
    text-align: center;
    vertical-align: top;
    .warpL {
      width: 150px;
      height: 100%;
      display: inline-block;
      background-color: green;
       ul {
         li {
           list-style: none;
         }
       }
    }
    .warpR {
      width: 490px;
      height: 100%;
      display: inline-block;
      background-color: blue;
      vertical-align: top;
       ul {
         li {
           text-align: left;
           list-style: none;
         }
       }
    }
  }
   .position {
     height: 40px;
     background-color: #f3f3f3;
     border: solid 1px #efefef;
     margin-bottom: 9px;
     .position-warp {
       width: 1209px;
       height: 100%;
       margin: 0 auto;
       line-height: 40px;
       color: #8a8a8a;
       font-size: 12px;
       position: relative;
       span:nth-child(1){
         display: inline-block;
         width: 13px;
         height: 19px;
         background: url("../assets/position.png");
         background-size: 100% 100%;
         margin-top: 3px;
         position: absolute;
         top: 50%;
         transform: translateY(-71%);
       }
       span:nth-child(2){
         display: inline-block;
         padding-left: 24px;
       }
     }

   }
   .submit {
     text-align: center;
     padding-bottom: 58px;
     padding-top: 22px;
   }
  .submit button {
    width: 397px;
    height: 42px;
    background-color: #008fff;
    border-radius: 5px;
    outline: none;
    border: none;
    color: #ffffff;
    font-size: 18px;
    cursor: pointer;
  }
</style>



